import Vue from 'vue';
import { Container, Aside, Header, Dropdown, DropdownMenu, DropdownItem, Main, Footer } from 'element-ui';
import { getMenuData } from 'common/menu';
import logo from 'assets/logo.png';
import SideMenu from './SideMenu.vue';
import GlobalFooter from 'components/GlobalFooter/index.vue';
import GlobalHeader from 'components/GlobalHeader/index.vue';
import SiderMenu from 'components/SiderMenu/index.vue';
Vue.use(Container);
Vue.use(Aside);
Vue.use(Header);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Main);
Vue.use(Footer);
/**
 * 根据菜单取得重定向地址.
 */
const redirectData = [];
const getRedirect = (item) => {
    if (item && item.children) {
        if (item.children[0] && item.children[0].path) {
            redirectData.push({
                from: `/${item.path}`,
                to: `/${item.children[0].path}`
            });
            item.children.forEach((children) => {
                getRedirect(children);
            });
        }
    }
};
getMenuData().forEach(getRedirect);
export default Vue.extend({
    name: 'BasicLayout',
    components: {
        SideMenu,
        GlobalFooter,
        GlobalHeader,
        SiderMenu
    },
    data() {
        // const menus: any = navData.reduce((prev: any, current: any) => {
        //   return prev.concat(current.children)
        // }, [])
        const menus = [];
        const openKeys = [];
        const footerLinks = [
            // {
            //   key: 'Element UI Pro',
            //   title: 'Element UI Pro',
            //   href: '',
            //   blankTarget: true
            // },
            {
                key: 'github',
                titleSlot: 'github-slot',
                href: 'https://github.com/element-pro/element-pro',
                blankTarget: true
            }
            // {
            //   key: 'Element UI',
            //   title: 'Element UI',
            //   href: 'http://element.eleme.io',
            //   blankTarget: true
            // }
        ];
        return {
            menus,
            logo,
            selectedKey: '',
            openKeys,
            collapsed: false,
            searchValue: '',
            footerLinks
        };
    },
    computed: {
        currentUser() {
            return this.$store.state.user.currentUser;
        },
        siderWidth() {
            return this.collapsed ? '64px' : '256px';
        }
    },
    watch: {
        collapsed(value) {
            this.openKeys = this.getDefaultCollapsedSubMenus();
        },
        searchValue(value) {
            console.log('search input', value);
        }
    },
    created() {
        this.selectedKey = this.getCurrentMenuSelectedKey();
        this.openKeys = this.getDefaultCollapsedSubMenus();
    },
    mounted() {
        this.$store.dispatch('user/fetchCurrent');
    },
    methods: {
        handleMenuCollapse(collapsed) {
            this.collapsed = collapsed;
        },
        getDefaultCollapsedSubMenus() {
            const currentMenuSelectedKey = this.getCurrentMenuSelectedKey();
            let keys = currentMenuSelectedKey.split('/').slice(1);
            if (keys.length === 1 && keys[0] === '') {
                keys = [this.menus[0].key];
            }
            keys.splice(-1, 1);
            let tempKey = '';
            const menuKeys = keys.map(key => {
                tempKey += `/${key}`;
                return tempKey;
            });
            return menuKeys;
        },
        getCurrentMenuSelectedKey() {
            const path = this.$route.path;
            if (!path) {
                return this.menus[0].key;
            }
            return path;
        },
        getMenuData() {
            return getMenuData();
        }
    }
});



