import Vue from 'vue';
import { Aside, Menu } from 'element-ui';
Vue.use(Aside);
Vue.use(Menu);
export default Vue.extend({
    props: {
        collapsed: {
            type: Boolean,
            default: false
        },
        menuData: {
            type: Array,
            default() {
                return [];
            }
        },
        logo: String
    },
    methods: {
        getMenuItem(h, item) {
            const elMenuItem = h('el-menu-item', {
                props: {
                    index: item.path
                }
            }, [
                item.icon && h('i', { class: `icon ${item.icon}` }),
                h('span', { slot: 'title' }, item.name)
            ]);
            const itemPath = this.conversionPath(item.path);
            if (/^https?:\/\//.test(itemPath)) {
                return h('a', {
                    attrs: {
                        href: itemPath,
                        target: item.target
                    }
                }, [elMenuItem]);
            }
            else {
                return h('router-link', {
                    props: {
                        to: itemPath
                    }
                }, [elMenuItem]);
            }
        },
        getSubMenuOrItem(h, item) {
            if (item.children && item.children.some((child) => child.name)) {
                return h('el-submenu', {
                    props: {
                        index: item.path
                    },
                    key: item.path,
                    scopedSlots: {
                        title: (props) => {
                            // return [
                            //   item.icon && h('i', { class: item.icon }),
                            //   h('span', { slot: 'title' }, item.name)
                            // ]
                            return h('span', item.name);
                        }
                    }
                }, [
                    h('template', {
                        slot: 'title'
                    }, [
                        item.icon && h('i', { class: `icon ${item.icon}` }),
                        h('span', item.name)
                    ])
                ].concat(this.getNavMenuItems(h, item.children)));
            }
            else {
                return this.getMenuItem(h, item);
            }
        },
        getNavMenuItems(h, menusData) {
            if (!menusData) {
                return [];
            }
            return menusData
                .filter(item => item.name && !item.hideInMenu)
                .map(item => {
                return this.getSubMenuOrItem(h, item);
            });
        },
        // conversion Path
        // 转化路径
        conversionPath(path) {
            if (path && path.indexOf('http') === 0) {
                return path;
            }
            else {
                return `/${path || ''}`.replace(/\/+/g, '/');
            }
        }
    },
    render(h) {
        const { collapsed, logo } = this;
        return h('el-aside', {
            class: {
                sider: true,
                collapse: collapsed
            },
            style: {
                minWidth: collapsed ? '64px' : '256px',
                maxWidth: collapsed ? '64px' : '256px'
            }
        }, [
            h('div', {
                class: 'logo'
            }, [
                h('router-link', {
                    props: {
                        to: '/'
                    }
                }, [
                    h('img', {
                        attrs: {
                            alt: 'logo',
                            src: logo
                        }
                    }),
                    h('h1', 'Element UI Pro')
                ])
            ]),
            h('el-menu', {
                props: {
                    mode: 'vertical',
                    'unique-opened': true,
                    collapse: collapsed
                },
                style: {
                    padding: '16px 0',
                    // width: collapsed ? '64px' : '256px'
                    width: '100%'
                }
            }, this.getNavMenuItems(h, this.menuData))
        ]);
    }
});



